import { Component, Input, OnInit, ViewChild, ViewContainerRef, ComponentFactoryResolver } from '@angular/core';
import { RX_VIEW_DEFINITION } from '@helix/platform/view/api';
import { RuntimeViewCanvasItemService } from '../canvas-item/runtime-view-canvas-item.service';
import { RuntimeViewCanvasItemContainerComponent } from '../canvas-item-container/runtime-view-canvas-item-container.component';

@Component({
  selector: 'rx-runtime-view-canvas-outlet',
  templateUrl: './runtime-view-canvas-outlet.component.html'
})
export class RuntimeViewCanvasOutletComponent implements OnInit {
  @Input() name = RX_VIEW_DEFINITION.defaultOutletName;
  @ViewChild('container', { read: ViewContainerRef, static: true }) container: ViewContainerRef;

  constructor(
    private runtimeViewCanvasItemService: RuntimeViewCanvasItemService,
    private componentFactoryResolver: ComponentFactoryResolver
  ) {}

  ngOnInit() {
    this.runtimeViewCanvasItemService.registerOutlet(
      this.name,
      this.container,
      this.componentFactoryResolver.resolveComponentFactory(RuntimeViewCanvasItemContainerComponent)
    );
  }
}
